import axios from 'axios';
import store from '../../../../store/index';
// 路由管理
// 登入相關的 api
const loginCheckRequest = axios.create({
  baseURL: `${process.env.VUE_APP_API_PATH}auth/check`,
});

loginCheckRequest.interceptors.request.use(
  confing => {
    store.commit('SET_LOADING', true);
    return confing;
  },
  error => {
    store.commit('SET_LOADING', false);
    return Promise.reject(error);
  },
);

//響應攔截
loginCheckRequest.interceptors.response.use(
  response => {
    store.commit('SET_LOADING', false);
    return response;
  },
  error => {
    store.commit('SET_LOADING', false);
    return Promise.reject(error);
  },
);

export default loginCheckRequest;
